<template>
  <div>
    <h3>:</h3>
    <el-table :data="linksData" style="width: 100%">
      <el-table-column prop="linksIntro" label=""></el-table-column>
      <el-table-column prop="link" label=""></el-table-column>
      <el-table-column fixed="right" label="" width="100">
        <template slot-scope="scope">
          <el-button
            @click="$router.push(`/blogManagerment/linksEdit/${scope.row._id}`)"
            type="text"
            size="small"
          ></el-button>
          <el-button @click="remove(scope.row)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>


<script>
export default {
  name: "tagList",
  data() {
    return {
      linksData: []
    };
  },
  methods: {
    async fetchTag() {
      const res = await this.axios.get("rest/links");
      this.linksData = res.data;
      console.log(this.linksData);
    },
    async remove(row) {
      this.$confirm(`${row.linksIntro}`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(async () => {
        const res = await this.axios.delete(`rest/links/${row._id}`);
        this.$message({
          type: "success",
          message: "!"
        });
        this.fetchTag();
      });
    }
  },
  created() {
    this.fetchTag();
  }
};
</script>

