<template>
  <div class="panel panel-default container">
    <h3>{{id?'':''}}</h3>
    <p class="blogTitle"></p>
    <input type="text" v-model="blogData.title" class="title form-control" />
    <p class="blogIntro"></p>
    <input type="text" v-model="blogData.Intro" class="title form-control" />
    <VueEditor
      v-model="blogData.markdown"
      id="edit"
      @image-added="handleImageAdded"
      useCustomImageHandler
    ></VueEditor>
    <button type="btn" class="btn btn-primary" v-on:click="saveDoc(),routerTo()"></button>
    <el-select class="select" v-model="blogData.parent" multiple placeholder="">
      <el-option v-for="item in parents" :key="item._id" :label="item.tagName" :value="item._id"></el-option>
    </el-select>
  </div>
</template>

<script>
import { VueEditor } from "vue2-editor";
export default {
  name: "sendBlog",
  props: {
    id: {}
  },
  components: {
    VueEditor
  },
  data() {
    return {
      blogData: {
        title: "",
        Intro: "",
        tagCloud: [],
        author: "",
        html: "",
        markdown: ""
      },
      parents: []
    };
  },
  methods: {
    async handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      const formData = new FormData();
      formData.append("file", file);
      const res = await this.axios.post("upload", formData);
      Editor.insertEmbed(cursorLocation, "image", res.data.url);
      resetUploader();
    },
    async fetchParents() {
      const res = await this.axios.get("rest/tags");
      this.parents = res.data;
    },
    saveDoc() {
      //ID
      //
      console.log(this.blogData);
      if (this.id) {
        console.log("put");
        let url = "rest/blogs/" + this.id;
        this.axios.put(url, this.blogData).then(data => {
          console.log(data);
        });
      } else {
        console.log("post");
        let url = "rest/blogs";
        this.axios.post(url, this.blogData).then(data => {
          console.log(data);
        });
      }
    },
    //ID
    async fetchEditBlog() {
      const res = await this.axios.get(`rest/blogs/${this.id}`);
      console.log(res);
      this.blogData = res.data;
    },
    routerTo() {
      this.$router.push({
        name: "deleteBlog"
      });
    }
  },
  mounted: function() {
    //ID
    if (this.$route.params.userId) {
      this.getBlog(this.$route.params.userId);
    }
  },
  created() {
    this.fetchParents();
    this.id && this.fetchEditBlog();
  }
};
</script>

<style scoped>
h1 {
  margin: 10px;
}

.blogTitle {
  text-align: left;
}

.blogIntro {
  text-align: left;
}

.title {
  margin-bottom: 10px;
}
.mavon {
  height: 70vh;
}

.container {
  display: inline-block;
  min-height: 90vh;
}

.btn {
  margin: 10px;
  float: right;
}

.card {
  border: 1px solid red;
}
#tag {
  float: left;
}
.select {
  margin-top: 10px;
  float: left;
}
</style>
